import axios from 'axios';
import path from './httpUrl';

// 获取轮播图的api，type：资源类型，对应以下类型，默认为0即PC
// 1: android   2: iphone   3: ipad
const getBanner = (type = 0) => {
    return axios.get(`${path.banner}?type=${type}`);
} 

// 获取推荐歌单，可选参数 : limit: 取出数量 , 默认为 30 (不支持 offset)
const getMusicList = (limit = 10) => {
    return axios.get(`${path.musicList}?limit=${limit}`);
}

// 获取歌单的详情
const getPlayListDetail = (id) =>  {
    return axios.get(`${path.playList}?id=${id}`);
}

// 检查音乐是否可用 说明: 调用此接口,传入歌曲 id, 可获取音乐是否可用,返回 { success: true, message: 'ok' } 或者 { success: false, message: '亲爱的,暂无版权' }
const checkMusicAvailable = (id) => {
    return axios.get(`${path.checkPlayID}?id=${id}`);
}

// 获取歌词 说明 : 调用此接口 , 传入音乐 id 可获得对应音乐的歌词 ( 不需要登录 )
const getLyric = (id) => {
    return axios.get(`${path.lyric}?id=${id}`);
}

// 搜索歌曲
const searchMusic = (keyword) => {
    return axios.get(`${path.search}?keywords=${keyword}`);
}

// 手机登录
const phoneLogin = (phone, password) => {
    return axios.get(`${path.login}?phone=${phone}&password=${password}`);
}

// 用户详情信息
const userDetail = (uid) => {
    return axios.get(`${path.userDetail}?uid=${uid}`);
}

export {
    getBanner,
    getMusicList,
    getPlayListDetail,
    checkMusicAvailable,
    getLyric,
    searchMusic,
    phoneLogin,
    userDetail
}