<template>
  <div class="musicList">
      <div class="musicList-top">
          <div class="title">发现好歌单</div>
          <div class="more">查看更多</div>
      </div>
      <div class="mlist">
          <div class="swiper-container" id="mySwiper">
            <div class="swiper-wrapper">
                <router-link :to="{path: '/listview', query: {id: item.id}}" tag="div" class="swiper-slide" v-for="(item) of state.musicList" :key="item.id">
                    <img :src="item.picUrl" :alt="item.name">
                    <div class="name">{{ item.name }}</div>
                    <div class="count">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-bofang1"></use>
                        </svg>
                        <span>{{ changeNum(item.playCount) }}</span>
                    </div>
                </router-link>
            </div>
            <!-- 如果需要分页器 -->
            <!-- <div class="swiper-pagination"></div> -->
        </div>
      </div>
  </div>
</template>

<script>
import 'swiper/css/swiper.css';
import Swiper from 'swiper'; 
import { getMusicList } from '@/api/index';
import { changeNum } from '@/libs/utils';
import { onMounted, reactive, toRefs, onUpdated } from 'vue';

export default {
    name: 'MusicList',
    setup(props, ctx) {
        const state = reactive({
            musicList: []
        });

       onMounted(async() => {
           // 发送请求
            let result = await getMusicList();
            state.musicList = result.data.result;
       });

       onUpdated(() => {
           var swiper = new Swiper("#mySwiper", {
                slidesPerView: 3,
                spaceBetween: 30,

                pagination: {
                    el: ".swiper-pagination",
                    clickable: true
                },
            });
       });

       return {
           state,
           changeNum
       }
    },
    async mounted() {

    }
}
</script>

<style lang="less" scoped>
    .musicList {
        width: 7.5rem;
        padding: 0 .4rem;

        /**********************************/
        .musicList-top {
            display: flex;
            justify-content: space-between;
            height: 1rem;
            align-items: center;

            .title {
                font-size: .4rem;
                font-weight: 900;
            }

            .more {
                border: 1px solid #ccc;
                padding: .06rem;
                border-radius: .2rem;
                font-size: .24rem;
                height: .5rem;
                width: 1.2rem;
                text-align: center;
                line-height: .35rem;
            }
        }
        
        /**********************************/
        .mlist {
            .swiper-container {
                width: 100%;
                height: 2.9rem;

                .swiper-slide {
                    display: flex;
                    flex-direction: column;
                    position: relative;


                    img {
                        width: 100%;
                        height: auto;
                        border-radius: .1rem;
                    }

                    .name {
                        height: .6rem;
                        width: 100%;
                        font-size: .24rem;
                        line-height: .3rem;
                        line-clamp: 2;
                    }

                    .count {
                        position: absolute;
                        right: .1rem;
                        top: .1rem;
                        font-size: .24rem;
                        color: #ccc;
                        display: flex;
                        align-items: center;

                        .icon {
                            width: .2rem;
                            height: .2rem;
                            fill: #ccc;
                        }
                    }
                }
            }
        }
    }
</style>