export default {
    lyricList: function(state) {
        let arr = state.lyric.split(/\n/igs).map((item, i) => {
            // 分隔符
            let colon = item.indexOf(':'),
                dot = item.indexOf('.'),
                lBracket = item.indexOf('['),
                rBracket = item.indexOf(']');

            let min = item.slice(lBracket + 1, colon),
                sec = item.slice(colon + 1, dot),
                mill = item.slice(dot + 1, rBracket),
                lyric = item.slice(rBracket + 1, item.length),
                time = parseInt(mill) + parseInt(sec) * 1000 + parseInt(min) * 60 * 1000;
            
            return {
                min, sec, mill, lyric, time
            }
        });

        // 再次处理设置pre
        arr.forEach((item, i) => {
            if(i === arr.length - 1) { // 如果上一个时间点不存在，就认为之前的时间点为0
                item.next = 0;
            } else {
                item.next = arr[i + 1].time;
            }
        });
        // console.log(arr);
        return arr;
    }
}