export default {
     // 设置好歌单的播放列表
    SET_PLAYLIST(state, playload) {
        state.playlist = playload;
        // console.log(state.playlist);
    },
    // 点击播放列表的某一项，实现控件播放该曲
    SET_PLAY_INDEX(state, idx) {
        state.playCurrentIndex = idx;
    },
    // 设置歌词
    SET_LYSIC(state, lyric) {
        state.lyric = lyric;
        // console.log(state.lyric);
    },
    // 设置当前时间
    SET_CURRENT_TIME(state, value) {
        state.currentTime = value;
    },
    // 追加搜索的播放歌曲到播放列表里
    PUSH_PLAYLIST(state, value) {
        state.playlist.push(value);
    },
    // 设置用户登录信息
    SET_USER(state, playload) {
        state.user = playload;
        localStorage.setItem('user', JSON.stringify(state.user));
    }
}