<template>
  <div class="app">
    <router-view/>
  </div>
</template>

<script>
import { onMounted, computed } from 'vue';
import { useStore } from 'vuex';
export default {
  name: 'App',
  components: {
    
  },
  setup() {
    onMounted(() => {
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      const store = useStore(),
            computedState = computed(() => store.state).value;
      computedState.user = user;
    });
  }
}
</script>

