import { checkMusicAvailable, getLyric, phoneLogin, userDetail } from '../api/index';

export default {
    // 设置好歌单的播放列表
    SET_PLAYLIST({ commit }, playload) {
        commit('SET_PLAYLIST', playload);
    },
    // 点击播放列表的某一项，实现控件播放该曲
    async SET_PLAY_INDEX({ commit }, { idx, itemId}) {
        console.log('start')
        let res = await checkMusicAvailable(itemId);
        console.log('res', res.data);
        // 检查判断请求的数据源是否成功，失败则不修改
        res.data.success ? commit('SET_PLAY_INDEX', idx) : '';
    },
    // 请求歌词
    async SET_LYSIC({ commit }, { id }) {
        let res = await getLyric(id);
        const lyric = res.data.lrc.lyric;
        // console.log(res);
        commit('SET_LYSIC', lyric);
    },
    // 更新播放歌词的时间
    UPDATE_TIME({ commit, state }, playload) {
        let currentTime;
        state.intervalId = setInterval(() => {
            currentTime = playload.value.currentTime; // 每一次都需要重新获取当前的时间
            commit('SET_CURRENT_TIME', currentTime);
        }, 1000);
    },
    // 追加搜索的播放歌曲到播放列表里
    PUSH_PLAYLIST({ commit }, value) {
        commit('PUSH_PLAYLIST', value);
    },
    // 手机登录
    async PHONE_LOGIN({ commit }, playload) {
        let { phoneNum, password } = playload;
        let res = await phoneLogin(phoneNum, password);
        console.log(res);
        if(res.data.code === 200) {
            let Detail = await userDetail(res.data.account.id);
            console.log(Detail);
            const playload = {
                Detail,
                isLogin: true,
                account: res.data.account
            }
            commit('SET_USER', playload);
        } 
        return res;
    }
}