<template>
  <div class="play-controller">
    <div class="left" @click="show=!show">
      <img :src="playlist[playCurrentIndex].al.picUrl" alt="">
      <div class="content">
        <div class="title">{{ playlist[playCurrentIndex].name }}</div>
        <div class="tips">横滑可以切换上下首哦</div>
      </div>
    </div>
    <div class="right">
      <svg v-if="ispaused" class="icon" aria-hidden="true" @click="play">
          <use xlink:href="#icon-bofang"></use>
      </svg>
      <svg v-else class="icon" aria-hidden="true" @click="play">
          <use xlink:href="#icon-zanting"></use>
      </svg>
      <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-24gf-playlist"></use>
      </svg>
    </div>
    <play-music :ispaused="ispaused" @back="show=!show" v-show="show" :playDetail="playlist[playCurrentIndex]"></play-music>
    <audio ref="audio" :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"></audio>
  </div>
</template>

<script>
import PlayMusic from '@/components/PlayMusic';
import { useStore } from 'vuex';
import { reactive, toRefs, computed, onMounted, ref, onUpdated, provide } from 'vue';
export default {
    name: 'PlayController',
    components: {
      PlayMusic
    },
    setup() {
      const store = useStore(),
            state = store.state,
            audio = ref(null),
            ispaused = ref(true),
            show = ref(false),
            computedState = computed(() => state).value;

      const play = () => {
        // console.log(audio.value.currentTime);
        if(audio.value.paused) { // 如果是暂停的话，点击播放
          audio.value.play();
          ispaused.value = false;
          // 播放马上启动定时器，每间隔1s更新当前时间
          store.dispatch('UPDATE_TIME', audio);
        } else { // 否则暂停
          audio.value.pause();
          ispaused.value = true;
          // 暂停立马清除定时器，不用修改当前时间
          clearInterval(computedState.intervalId);
        }
      }

      provide('audio', audio);
      provide('ispaused', ispaused);
      provide('play', play);


      onMounted(() => {
        // console.log(audio.value.volume);
        // console.log(computedState.playlist);
        // console.log(computedState.playCurrentIndex);
        // 
        const playload = {
          id: computedState.playlist[computedState.playCurrentIndex].id
        }
        store.dispatch('SET_LYSIC', playload);
      });

      onUpdated(() => {
        // 索引值变化后重新申请歌词
        const playload = {
          id: computedState.playlist[computedState.playCurrentIndex].id
        }
        store.dispatch('SET_LYSIC', playload);
      });

      return {
        ...toRefs(computedState),
        audio,
        play,
        ispaused,
        show
      };
    }
}
</script>

<style lang="less" scoped>
  .play-controller {
    width: 7.5rem;
    height: 1.2rem;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ccc;
    background-color: #fff;
    z-index: 10;
    /*********************************** */
    .left {
      display: flex;
      padding: 0.1rem;
      img {
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 50%;
      }

      .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 0.2rem;

        .title {
          width: 3.6rem;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .tips {
          font-size: 0.2rem;
          color: #999;
        }
      }
    }
    /*********************************** */
    .right {
      .icon {
        width: 0.5rem;
        height: 0.5rem;
        margin: 0 0.2rem;
      }
    }
  }
</style>