<template>
    <div class="swipper-box">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div 
                    class="swiper-slide"
                    v-for="(item, index) of imgs"
                    :key="index"
                >
                    <img :src="item.pic" alt="img">
                </div>
            </div>
            <!-- 如果需要分页器 -->
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import 'swiper/css/swiper.css';
import Swiper from 'swiper';   
import axios from 'axios';
import { onMounted, reactive, toRefs } from 'vue';
import { getBanner } from '@/api/index';

export default {
    name: 'SwiperCom',
    setup(props, ctx) {
        const state = reactive({
            imgs: [
                { pic: require('@/assets/img/swiper1.jpg') },
                { pic: require('@/assets/img/swiper2.jpg') },
                { pic: require('@/assets/img/swiper3.jpeg') },
            ]
        });

        onMounted(async() => {
            var mySwiper = new Swiper ('.swiper-container', {
                loop: true, // 循环模式选项
                
                // 如果需要分页器
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                }
            });

            // 发送请求, 获取轮播图数据
            let res = await getBanner(1);
            state.imgs = res.data.banners;
        });
        return {
            ...toRefs(state)
        }
    },
    // async mounted() { // 轮播图有点异常
        
    // }
}
</script>

<style lang="less" scoped>
    .swipper-box {
        width: 7.5rem;

         .swiper-container {
            margin: 0 auto;
            width: 7.1rem;
            height: 2.6rem;
            border-radius: .1rem;

            .swiper-slide img {
                width: 100%;
            }

            --swiper-pagination-color: orangered;
            .swiper-pagination-bullet-active {
                background-color: orangered;
            }
        }
    }
   
</style>