// 将播放量转化为数字+单位
const changeNum = (num = 0) => {
    let res = num;
    if(num >= 100000000) {
        res = (num / 100000000).toFixed(2) + '亿';
    } else if(num >= 10000) {
        res = (num / 10000).toFixed(2) + '万';
    }

    return res;
}

// 播放音乐playmusic


// 防抖
const debounce = function(fn, delay) {
    let timer = null;

    return function() {
        clearTimeout(timer);
        
        timer = setTimeout(() => {
            fn.apply(this, arguments);
        }, delay);
    }
}


export {
    changeNum,
    debounce
}

