<template>
  <div class="playMusic">
      <div class="bg" :style="{ backgroundImage: `url(${playDetail.al.picUrl})`}"></div>
      <div class="playTop">
          <div class="back" @click="backTo">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-zuojiantou"></use>
              </svg>
          </div>
          <div class="center">
              <div class="title">
                  <marquee behavior="scroll" direction="left">{{ playDetail.al.name }}</marquee>
                  <!-- <div class="ma">{{ playDetail.al.name }}</div> -->
              </div>
          </div>
          <div class="share">
              <svg class="icon icon-shared" aria-hidden="true">
                <use xlink:href="#icon-fenxiang"></use>
              </svg>
          </div>
      </div>
      <div class="playContent" v-show="!isLyric" @click="isLyric=!isLyric">
          <img :class="{'needle': true, active: !ispaused}" src="@/assets/img/needle.png" alt="needle">
          <img class="disc" src="@/assets/img/disc2.png" alt="disc">
          <img :class="['album', { active: !ispaused}]" :src="playDetail.al.picUrl" alt="album">
      </div>
      <div class="playLyric" v-show="isLyric" @click="isLyric=!isLyric" ref="playLyric">
          <p :class="{active: canPlay(currentTime, item)}" v-for="(item, i) in $store.getters.lyricList" :key="i">{{ item.lyric }}</p>
      </div>
      <div class="progress"></div>
      <div class="playFooter">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-xunhuan"></use>
          </svg>
          <svg class="icon" aria-hidden="true"  @click="goPlay(-1)">
            <use xlink:href="#icon-prev"></use>
          </svg>
          <svg v-if="ispaused" class="icon" aria-hidden="true" @click="play">
            <use xlink:href="#icon-bofang"></use>
          </svg>
          <svg v-else class="icon" aria-hidden="true" @click="play">
            <use xlink:href="#icon-zanting"></use>
          </svg>
          <svg class="icon" aria-hidden="true" @click="goPlay(1)">
            <use xlink:href="#icon-next"></use>
          </svg>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-bofangliebiao"></use>
          </svg>
      </div>
  </div>
</template>

<script>
import { inject, ref, computed, toRefs, watch } from 'vue';
import { useStore } from 'vuex';
export default {
    name: 'PlayMusic',
    props: ['playDetail'],
    emits: ['back'],
    setup(props, { emit }) {
        const backTo = () => {
            emit('back', 'show');
        }
        const audio = inject('audio'),
              ispaused = inject('ispaused'),
              play = inject('play'),
              isLyric = ref(true),
              playLyric = ref(null);

        const store = useStore(),
              state = store.state,
              computedState = computed(() => state).value;

        const canPlay = (currentTime, item) => {
            // console.log('-->', currentTime,'-->',  item.time,'-->',  item.next);
            // 如果当前时间在歌词的初始时间和下一句的范围内，激活样式
            return (currentTime*1000 >= item.time && currentTime*1000 < item.next);
        }

        // 监听歌词变化，设置滚动条
        watch(() => {
            return computedState.currentTime;
        }, (newVal, oldVal) => {
            console.log(oldVal, newVal);
            console.log([playLyric.value]);
            // 找到激活状态的歌词
            let p = document.querySelector('p.active');
            console.log(p);
            // 歌词段距离盒子的顶部的距离
            let offsetTop = p ? p.offsetTop : 0;
            // // 盒子的高度
            // let h = playLyric.value.offsetHeight;
            // if(offsetTop > h) { // 如果p的距离不在视口内，将其提升到视口中间
            //     playLyric.value.scrollTop = playLyric.value.scrollTop + (offsetTop - h / 2);
            // }

            playLyric.value.scrollTop = offsetTop;
        });
        
        const goPlay = (num) => {
            console.log(num);
            console.log(computedState.playlist);
            console.log(computedState.playCurrentIndex);
            let idx = computedState.playCurrentIndex + num;
            idx = idx < 0 ? (computedState.playlist.length - 1) : (idx === computedState.playlist.length ? 0 : idx);
            let itemId = computedState.playlist[idx].id;
            const playload = {
                idx,
                itemId
            }
            // 设置歌词为新索引
            store.dispatch('SET_PLAY_INDEX', playload);
        }

        return {
            backTo,
            play,
            ispaused,
            isLyric,
            ...toRefs(computedState),
            canPlay,
            playLyric,
            goPlay
        }
    }
}
</script>

<style lang="less" scoped>
    .playMusic {
        position: fixed;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background-color: #fff;
        
        .bg { // 背景模糊图片
            position: absolute;
            left: 0;
            top: 0;
            width: 100vw;
            height: 100vh;
            background-size: auto 100%;
            filter: blur(80px);
            
        }

        .playTop {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 7.5rem;
            height: 1.2rem;
            position: absolute;
            left: 0;
            top: 0;
            color: #fff;
            padding: 0 0.4rem;

            .icon {
                fill: #fff;
                width: .6rem;
                height: .6rem;
            }

            .icon-shared {
                width: .45rem;
                height: .45rem;
            }

            marquee {
                width: 5rem;
                flex: 1;
            }
        }
        /*********************************************** */
        .playContent {
            position: absolute;
            width: 7.5rem;
            height: 7.5rem;
            left: 0;
            top: 1.5rem;

            .needle {
                width: 2.5rem;
                height: auto;
                position: absolute;
                left: 3.2rem;
                transform-origin: top left;
                transform: rotate(-15deg);
                transition: all 1s;
                z-index: 10;
            }

            .needle.active {
                transform: rotate(2deg);
            }

            .disc {
                width: 5.5rem;
                height: auto;
                position: absolute;
                left: calc(50% - 2.75rem);
                top: 2.5rem;
                // background-color: #eee;
                // border-radius: 50%;
                // filter: blur(1px);

                
            }

            .album {
                width: 3.36rem;
                height: 3.36rem;
                border-radius: 50%;
                position: absolute;
                left: calc(50% - 1.66rem);
                top: 3.59rem;
                transition: all 2s;
                &.active {
                    animation: spin 3s infinite linear forwards;
                    transform: rotate(720deg);
                }

                @keyframes spin {
                    0% {
                        transform: rotate(0deg);
                    }
                    100% {
                        transform: rotate(1turn);
                    }
                }
            }
        }
        /*********************************************** */
        .playFooter {
            width: 7.5rem;
            height: 1.5rem;
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 0.4rem;

            .icon {
                fill: #fff;
                width: 0.6rem;
                height: 0.6rem;
            }

            .icon.playsong {
                width: 1rem;
                height: 1rem;
            }
        }

        .playLyric {
            position: absolute;
            width: 7.5rem;
            height: 8rem;
            left: 0;
            top: calc(50% - 4rem);
            overflow: scroll;
            text-align: center;
            color: #fff;
            padding: 0.2rem 0;

            .active {
                color: red;
            }
        }
    }
</style>